import React from 'react';
import { LineChart, Line, XAxis, YAxis, CartesianGrid, Tooltip, Legend, ResponsiveContainer } from 'recharts';

interface Data {
    name: string;
    aprovaes: number;
    trabalhos: number;
    recomendaes: number;
}

const data: Data[] = [
    { name: 'Maio', aprovaes: 18, trabalhos: 20, recomendaes: 10 },
    { name: 'Abr', aprovaes: 17, trabalhos: 18, recomendaes: 9 },
    { name: 'Mar', aprovaes: 16, trabalhos: 19, recomendaes: 9 },
    { name: 'Fev', aprovaes: 15, trabalhos: 17, recomendaes: 8 },
    { name: 'Jan', aprovaes: 14, trabalhos: 16, recomendaes: 8 },
    { name: 'Dez', aprovaes: 13, trabalhos: 15, recomendaes: 8 },
    { name: 'Nov', aprovaes: 14, trabalhos: 16, recomendaes: 9 },
    { name: 'Out', aprovaes: 15, trabalhos: 17, recomendaes: 9 },
    { name: 'Set', aprovaes: 16, trabalhos: 18, recomendaes: 10 },
    { name: 'Ago', aprovaes: 17, trabalhos: 19, recomendaes: 10 },
    { name: 'Jul', aprovaes: 15, trabalhos: 17, recomendaes: 9 },
    { name: 'Jun', aprovaes: 14, trabalhos: 16, recomendaes: 8 },
];

const Graph: React.FC = () => {
    return (
        <ResponsiveContainer width="100%" height={400}>
            <LineChart
                data={data}
                margin={{ top: 5, right: 30, left: 20, bottom: 5 }}
            >
                <CartesianGrid strokeDasharray="3 3" />
                <XAxis dataKey="name" />
                <YAxis />
                <Tooltip />
                <Legend />
                <Line type="monotone" dataKey="recomendaes" stroke="#098037" />
                <Line type="monotone" dataKey="aprovaes" stroke="#241d9c" />
                <Line type="monotone" dataKey="trabalhos" stroke="#0053ee" />
            </LineChart>
        </ResponsiveContainer>
    );
};

export default Graph;
